<template>
    <div class="sworks">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-folder-opened"></i> 基础资料
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form">
                <div class="form-top">
                    <el-button class="fr" type="primary" @click="editVisible3=true" size="small">新建文件夹</el-button>
                </div>
                <div class="cont">
                    <el-table
                      :data="tableData"
                      style="width: 100%;margin-top:20px;">
                      <el-table-column
                        type="index"
                        width="100px"
                        label="序号">
                      </el-table-column>
                      <el-table-column
                        prop="name"
                        label="文件夹名称">
                      </el-table-column>
                      <el-table-column
                        prop="address"
                        label="文件数量">
                      </el-table-column>
                      <el-table-column
                        prop="address"
                        label="创建时间">
                      </el-table-column>
                      <el-table-column label="操作" width='180px'>
                          <template slot-scope="scope">
                            <el-button
                              size="mini"
                              type="primary" plain
                              @click="handleEdit">管理文件</el-button>
                            <el-button
                              size="mini"
                              type="danger"
                              plain
                              @click="delworks">删除</el-button>
                          </template>
                        </el-table-column>
                    </el-table>

                    <div class="pagination">
                        <el-pagination
                          background
                          layout="prev, pager, next"
                          :total="1000">
                        </el-pagination>
                    </div>
                    
                </div>
            </div>
        </div>
        <!-- 编辑弹出框 -->
        <el-dialog title="添加资料" class="datas-bg" :visible.sync="editVisible" width="800px">
            <div class="select-bg">
                <el-button size="small" style="margin-bottom:10px;float:right;" type="primary" @click="saveEdit">上传文件</el-button>
            </div>
            <el-table
                size="mini"
                ref="multipleTable"
                class="editlist"
                :data="tableData"
                tooltip-effect="dark"
                height="300px"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                  prop="name"
                  label="序号">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="文件名称">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="文件大小">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="上传者">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="上传时间">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="使用次数">
                </el-table-column>
                <el-table-column label="操作" >
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      type="danger"
                      @click="delworks">删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div class="pagination">
                <el-pagination
                  background
                  layout="prev, pager, next"
                  :total="1000">
                </el-pagination>
            </div>
        </el-dialog>
        <!-- 新建入学年份 -->
        <el-dialog title="新建文件夹" class="datas-bg" :visible.sync="editVisible3" width="380px">
            <el-input class="margin10" v-model="input" size="small" placeholder="文件夹名称"></el-input>
            <div class="buttonbg" >
              <el-button type="primary" size="small">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible3=false" plain>取 消</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'course',
    data() {
        return {
            editVisible:false,
            editVisible3:false,
            choosed:'未完成课程设计教学',
            items:[1,1,1],
            tableData: [{
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
              }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1517 弄'
              }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1519 弄'
              }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1516 弄'
              }]
        };
    },
    methods: {
        openLink(){
            this.$router.push({path:'newworks'})
        },
        handleEdit(){
            this.editVisible=true;
        },
        delworks(){
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.$message.success('删除成功');
                this.tableData.splice(index, 1);
            })
            .catch(() => {});
        }
    }
};
</script>
<style lang='scss'>
    .datas-bg{
         .el-dialog__body{
            padding:10px 20px 30px;
         }
    }
</style>
<style lang='scss' scoped>
    .sworks{
        .form-top{
            overflow:hidden;
        }
    }
    .pagination{
        margin-top:20px;
    }
    .buttonbg{
      text-align:center;
      padding-top: 20px;
    }
</style>


